<template>
    <div class="api">
        <h2>History API</h2>
        <div class="api-info">
            <h3>History API</h3>
            <p>The History API lets us manipulate session history in the browser to keep track of the user’s activity. This API is included in the official specification as the History interface.
            </p>
        </div>
        <pre class="api-code">
	window.history; // History object
        </pre>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>go(n)</td>
                <td>We goes back or forward on the number ('n') of steps in the session history. If the 'n' is zero then will reload the current page. If the 'n' is out of range, does nothing.</td>
            </tr>
            <tr>
                <td>back()</td>
                <td>Goes back one step. If there is no previous page, does nothing.</td>
            </tr>
            <tr>
                <td>forward()</td>
                <td>Goes forward one step. If there is no next page, does nothing.</td>
            </tr>
            <tr>
                <td>pushState(data, title [, url ]) </td>
                <td>This method creates a new entry in the session history. Pushes the given data onto the session history, with the given title, and, if provided, the given URL.</td>
            </tr>
            <tr>
                <td>replaceState(data, title [, url ])</td>
                <td>This method works exactly like pushState(), but it doesn’t generate a new entry. Instead, it replaces the information for the current one.</td>
            </tr>
        </table>
        <pre class="api-code">
	window.history.go(-1); // or window.history.back(); 
	window.history.forward(); 
	window.history pushState(2, ‘Title’, 'history.html'); 
	window.history.replaceState({foo: 'bat'}, 'New Title');
        </pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>length</td>
                <td>Returns the total number of entries in the history list.</td>
            </tr>
            <tr>
                <td>state</td>
                <td>Returns the state for the current URL.</td>
            </tr>
        </table>
        <pre class="api-code">
    var n=window.history.length // example 4
        </pre>
        <h4 class="api-table-caption">The Events</h4>
        <table class="api-table">
            <tr>
                <td>popstate</td>
                <td>This event is fired in certain circumstances to inform the current state.</td>
            </tr>
        </table>
        <pre class="api-code">
	window.addEventListener('popstate', function(e){ 
	    // function() 
	}, false);
        </pre>
    </div>
</template>
<script>
export default {
    name: 'history',
    data() {
        return {}
    }
}
</script>



